input:focus,
input:hover{
  border-color: #FFD700!important;
  cursor: pointer;
}
input::placeholder {
  color: white;
  text-align: center;
  font-size: 15px;
  text-transform: none!important;
}
input{
  text-align: center;
  font-size: 15px;
  text-transform: uppercase;
}
